* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  font-size: 100px;
}

.container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  flex-basis: 300px;
  flex-grow: 1;
  flex-shrink: 0;
  text-align: center;
  line-height: 300px;
}

@media (min-width: 600px) {
  .container {
    flex-direction: row;
  }

  .block {
    flex-basis: 50%;
  }

  .block--1 {
    order: 1;
  }

  .block--2 {
    order: 2;
  }

  .block--4 {
    order: 3;
  }

  .block--3 {
    order: 4;
  }

  .block--5 {
    order: 5;
  }

  .block--6 {
    order: 6;
  }
}

@media (min-width: 1050px) {
  .container {
    flex-direction: row;
  }

  .block {
    flex-basis: 33.33%;
  }

  .block--1 {
    order: 1;
  }

  .block--2 {
    order: 2;
  }

  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 6;
  }

  .block--5 {
    order: 5;
  }

  .block--6 {
    order: 4;
  }
}

.block--1 {
  background-color: red;
}

.block--2 {
  background-color: rgb(204, 0, 0);
}

.block--3 {
  background-color: rgb(153, 0, 0);
}

.block--4 {
  background-color: rgb(102, 0, 0);
}

.block--5 {
  background-color: rgb(51, 0, 0);
}

.block--6 {
  background-color: rgb(0, 0, 0);
}
